const Images = ({ imgIndex }) => {
  return (
    <>
      {imgs.map((card, idx) => {
        return (
          <motion.div
            key={idx}
            // style={{
            //   backgroundImage: `url(${imgSrc})`,
            //   backgroundSize: "cover",
            //   backgroundPosition: "center",
            // }}
            animate={{
              scale: 0.85,
            }}
            transition={SPRING_OPTIONS}
            className="aspect-video lg:w-1/2 w-full shrink-0 rounded-xl h-[50vh] flex justify-center items-center  object-cover"
          >
            <TiltCard head={card.head} paragraph={card.paragraph} svg={card.svg} />


          </motion.div>
        );
      })}
    </>
  );
};

const Dots = ({ imgIndex, setImgIndex }) => {
  const halfImgs = imgs.slice(0, Math.ceil(imgs.length / 2));
  return (
    <div className="mt-4 flex w-full justify-center gap-2">
      {halfImgs.map((_, idx) => {
        return (
          <button
            key={idx}
            onClick={() => setImgIndex(idx)}
            className={`h-3 w-3 rounded-full transition-colors ${idx === imgIndex ? "bg-bg-orange" : "bg-neutral-500"
              }`}
          />
        );
      })}
    </div>
  );
};

const GradientEdges = () => {
  return (
    <>
      <div className="pointer-events-none absolute bottom-0 left-0 top-0 w-[5vw]  " />
      <div className="pointer-events-none absolute bottom-0 right-0 top-0 w-[5vw] " />
    </>
  );
};


const TiltCard = (props) => {
  const x = useMotionValue(0);
  const y = useMotionValue(0);

  const mouseXSpring = useSpring(x);
  const mouseYSpring = useSpring(y);

  const rotateX = useTransform(
    mouseYSpring,
    [-0.5, 0.5],
    ["17.5deg", "-17.5deg"]
  );
  const rotateY = useTransform(
    mouseXSpring,
    [-0.5, 0.5],
    ["-17.5deg", "17.5deg"]
  );

  const handleMouseMove = (e) => {
    const rect = e.target.getBoundingClientRect();

    const width = rect.width;
    const height = rect.height;

    const mouseX = e.clientX - rect.left;
    const mouseY = e.clientY - rect.top;

    const xPct = mouseX / width - 0.5;
    const yPct = mouseY / height - 0.5;

    x.set(xPct);
    y.set(yPct);
  };

  const handleMouseLeave = () => {
    x.set(0);
    y.set(0);
  };

  return (
    <motion.div
      onMouseMove={handleMouseMove}
      onMouseLeave={handleMouseLeave}
      style={{
        rotateY,
        rotateX,
        transformStyle: "preserve-3d",
      }}
      className="relative lg:h-96 lg:w-72 w-full h-full rounded-xl bg-gradient-to-br from-[#FF5327] to-[#f57656]"
    >
      <div
        style={{
          transform: "translateZ(75px)",
          transformStyle: "preserve-3d",
        }}
        className="absolute inset-4 grid place-content-center rounded-xl bg-white shadow-lg"
      >

        <div className="relative z-10 px-2">
          <div className="bg-white w-16 h-16 mb-2 rounded-full text-3xl text-indigo-600 grid place-items-center mx-auto">
            <img src={props.svg} className="w-full h-full rounded-full object-cover" />
          </div>
          <h3 className="text-3xl font-bold text-center mb-2">
            {props.head}
          </h3>
          <p className="text-center mb-6">
            {props.paragraph}
          </p>

        </div>

      </div>
    </motion.div>
  );
};